.App {
  text-align: center;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

.nav-container {
  text-align: center;
}

.mynav {
  opacity: 0.8;
  position: static;
}

.my-lottie {
  height: 80%;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  
}

.navbrand {
  text-align: center;
  position: relative;
  left: 15px;
}

.navLinks {
  text-align: center;
}

div#map-size {
  width: 100%;
  min-width: 100%;
  height: 70vw;
  position: relative;
  left: 0;
  opacity: 0.8;
}

video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -10;
}

@media (min-aspect-ratio: 16/9) {
  #bg-vid {
      width:100%;
      height: auto;
  }
}
@media (max-aspect-ratio: 16/9) {
  #bg-vid { 
      width:auto;
      height: 100%;
  }
}

@media (max-width: 767px) {
  #bg-vid {
      display: none;
  }
  body {
      background: url('./assets/bg-poster.png');
      background-size: cover;
  }
}


/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) { 


  .navbrand {
    position: relative;
    right: 10vw;
  }
  /* .navLinks {
    position: relative;
    left: 0;
  }
  .my-lottie {
    position: relative;
    left: 0;
  } */
 }

 /* Medium devices (tablets, 768px and up) */
 @media (min-width: 768px) {  


  div#map-size {
    width: 50vw;
    height: 50vw;
    left: 20vw;
  }
  
  .navLinks {
    position: relative;
    left: -30vw;
  } 

  .my-lottie {
    position: relative;
    left: -20%;
  }

  .navbrand {
    position: relative;
    left: -5%;
  }

 }

